/* global gapi */
import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';
import { graphql } from 'react-apollo';
import IconButton from '@material-ui/core/IconButton';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { FormControlLabel, Checkbox, Typography, Button, Fab } from '@material-ui/core';
import { FaFacebookF, FaGoogle } from 'react-icons/fa';
import { Link } from 'react-router-dom';
import { gql } from 'apollo-boost';
import { ValidatorForm} from 'react-material-ui-form-validator';
import { withRouter } from 'react-router';
import { GoogleLogin } from 'react-google-login';
import * as compose from 'lodash.flowright';

import './login.style.css';
import Logo from '../../images/logo.png';

class  Login extends Component{
    constructor(props){
        super(props);
        this.state = {
            showPassword: false,
            checkedB: false,
            email : '',
            password : '',
            error: '',
            user : '',
            code : '',
            bearerTokenResponse : '',
            to : '',
            userType : '',
            loginFrom : 'local'
        }
    }
    componentDidMount(){
        console.log(localStorage);
        if(localStorage.userType ){
            this.props.history.push({
                pathname: '/homepage',
                state :localStorage.userType
            });   
            }
        else if(localStorage.jwt){
            this.props.history.push({
                pathname: '/homepage',
                state :localStorage.jwt
            }); 
         }
        }
    render (){
        const handleChangePassword = prop => event => {
            this.setState({ ...this.state, [prop]: event.target.value });
        };
        const handleChangeEmail = prop => event => {
            this.setState({ ...this.state, [prop]: event.target.value });
        };
        const handleClickShowPassword = () => {
            this.setState({ ...this.state, showPassword: !this.state.showPassword });
        };
    
        const handleMouseDownPassword = event => {
            event.preventDefault();
        };
        const handleChangeC = name => event => {
            this.setState({ ...this.state, [name]: event.target.checked });
        };
        const responseGoogle = (response) => {
          //  console.log(response.accessToken);
            this.setState({...this.state,accessToken : response.accessToken });
            this.props.loginSocial({
                variables : {
                    token : this.state.accessToken,
                    provider : "google"
                }
            }).then((res)=>{
                console.log(res);
                // if not loged in before 
                if(res.data.loginSocialite.user === null){
                    this.props.history.push({
                        pathname: '/registration',
                        state : response
                    });
                }
                else {
                    alert("done!");
                }
            })
          }
    return (
            <div className="container">
                <ValidatorForm      
                    onSubmit={e => {
                    e.preventDefault();
                    // if(ValidateEmail(this.state.email)){
                        this.props.login({
                            variables : {
                                email : this.state.email,
                                password : this.state.password
                            }
                        }).then((res)=>
                        {   
                            if(res.data.login.user == null){
                                this.setState({...this.state, error : res.data.login.Status.message })
                            }
                            else {
                                this.setState({...this.state,error : res.data.login.Status.message, user :res.data.login.user ,code : res.data.login.Status.code , bearerTokenResponse :res.data.login.BearerTokenResponse , userType : res.data.login.user.typeName })
                                localStorage.setItem('jwt',res.data.login.user.typeName);
                                this.props.history.push({
                                    pathname: '/homepage',
                                    state : this.state.userType
                                });
                                console.log(localStorage.jwt);
                            }
                            if(this.state.checkedB === true){
                                localStorage.setItem('userType', this.state.userType);
                                }
                        } )
                        .catch((err)=>this.setState({...this.state, error : "Check your internet connection" }));                   
                    // }
                    }}
                     className="container pt-10 pb-1" autoComplete="off">
                    <div className="pt-1 pb-3">
                        <img src={Logo} alt="spreago logo" width="150px"/>
                    </div>
                    <div className="pb-3">
                        <Typography variant="h4" color="primary">Welcome Back</Typography>
                    </div>
                    <Row className="justify-content-md-center mt-3">
                        <Col className="col-md-auto">
                              <GoogleLogin
                                    clientId="468100560729-50aef0qmq47uv5tm413g8l6ijqmrrego.apps.googleusercontent.com"
                                    buttonText="Login"
                                    onSuccess={responseGoogle}
                                    onFailure={responseGoogle}
                                    cookiePolicy={'single_host_origin'}
                                />
                        </Col>
                        <Col md={12}>
                                <h6 className="dividor-container pt-3 pb-1"><span>Or login with</span></h6>
                        </Col>
                    </Row>
                    <TextField
                        fullWidth
                        required
                        label="Email"
                        type ="email"
                        color="primary"
                        variant="outlined"
                        value={this.state.email}
                        onChange={handleChangeEmail('email')}
                    />
                    <FormControl required  fullWidth className="mt-3" variant="outlined">
                        <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password"
                            type={this.state.showPassword ? 'text' : 'password'}
                            value={this.state.password}
                            onChange={handleChangePassword('password')}
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            }
                            labelWidth={70}
                        />
                    </FormControl>
                    <div className="text-danger"><p>{this.state.error}</p></div>
                    <Row className="justify-content-md-center">
                        <Col lg={4} xs={6}>
                            <FormControlLabel
                                className="d-inline-block"
                                control={
                                    <Checkbox
                                        checked={this.state.checkedB}
                                        onChange={handleChangeC('checkedB')}
                                        value="checkedB"
                                        label="Remember me"
                                        color="primary"
                                    />
                                }
                                label="Remember me"
                            />
                        </Col>
                        <Col lg={{ span: 4, offset: 4 }} xs={6}>
                            <Typography  className="d-inline-block mt-1 pt-1">
                                <Link to='/forgetPassword'  className="text-orange">
                                     Forgot Password?
                                </Link>
                            </Typography>
                        </Col>
                        <Col xs={6} className="text-right">
                            <Button type="submit" size="large" className="btn-orange-gradiant pr-5 pl-5" variant="contained" to={this.state.to} >Login</Button>
                        </Col>
                        <Col xs={6} className="text-left">
                            <Button type="button" size="large" className="btn-white-gradiant pr-5 pl-5"  variant="contained" 
                            onClick={e=>{
                                    this.props.history.push({
                                        pathname:'/createAccount'
                                    });
                            }} color="secondary" >Register</Button>
                        </Col>
                        {/* <Col className="text-right">
                            <Fab size="medium" variant="outlined" disabled   className="btn-social bg-white" aria-label="add">
                                <FaGoogle size="1.5em" />
                            </Fab>
                        </Col>
                        <Col className="text-left">
                            <Fab size="medium" variant="outlined" disabled  className="btn-social bg-white" aria-label="add">
                                <FaFacebookF size="1.5em" />
                            </Fab>
                        </Col> */}
                        {/* <Col className="text-center">
                            <div  className="btn-fb box-shadow-none text-center pt-1 rounded pb-1">
                                <div className="fb-login-button text-center" data-width="" data-size="medium" data-button-type="login_with" data-auto-logout-link="false" data-use-continue-as="false"></div>
                            </div>
                        </Col> */}
                    </Row>
                        
                </ValidatorForm>
            </div>
    );
    }
   
}
const LOGIN = gql`
mutation( $email : Email ! $password : String ! ){
   login(data: {
     attempt:{
       email: $email
       password: $password 
     }
   }){
     BearerTokenResponse{
       access_token
       token_type
     }
     user{
        name
        typeName
      }
     Status{
       message
       code
       check
       title
     }
   }
 }
`;

const LOGIN_SOCIAL = gql `
mutation( $token : String ! $provider : String ! ){
    loginSocialite(data: {
      SocialiteInput:{
        token : $token
        provider : $provider
      }
    }){
      user{
        name
      }
      Status{
        message
        code
      }
      __typename
    }
  }
`
export default compose(
    graphql(LOGIN, {
    name : 'login'
  }),
    graphql(LOGIN_SOCIAL, {
    name: 'loginSocial'
  })
)(withRouter(Login));